import React from 'react';
import './Second.css';

const Second = () => {
    
    let classTags = ['active'];

    const ActiveElement = (event) => {
        const elementId = event.target.id;
        switch(elementId){
            case 'interesttopics': 
                document.getElementById(elementId).classList.add('active')
                document.getElementById('interestpeople').classList.remove('active')
                document.getElementById('interestcommunities').classList.remove('active')
                document.getElementById('intereststags').classList.remove('active')
                break;
            case 'interestpeople': 
                document.getElementById('interesttopics').classList.remove('active')
                document.getElementById(elementId).classList.add('active')
                document.getElementById('interestcommunities').classList.remove('active')
                document.getElementById('intereststags').classList.remove('active')
                break;
            case 'interestcommunities': 
                document.getElementById('interesttopics').classList.remove('active')
                document.getElementById('interestpeople').classList.remove('active')
                document.getElementById(elementId).classList.add('active')
                document.getElementById('intereststags').classList.remove('active')
                break;
            case 'intereststags': 
                document.getElementById('interesttopics').classList.remove('active')
                document.getElementById('interestpeople').classList.remove('active')
                document.getElementById('interestcommunities').classList.remove('active')
                document.getElementById(elementId).classList.add('active')
                break;
            default: 
                console.log('wrong input');
        }
        console.log(classTags.join(' '))
    }

    return(
        <div className="InterestsSecond">
            <div className="OptionBox">
                <div className="Options">
                    <button id="interesttopics" className='active' onClick={ActiveElement}>TOPICS</button>
                    <button id="interestpeople" className='' onClick={ActiveElement}>PEOPLE</button>
                    <button id="interestcommunities" className='' onClick={ActiveElement}>COMMUNITIES</button>
                    <button id="intereststags" className='' onClick={ActiveElement}>HASHTAGS</button>
                </div>
                <hr></hr>
            </div>
        </div>
    )
}

export default Second