import React from 'react';
import './Notification.css';
import {Popover} from 'react-bootstrap';


const Notification = () => {
    return(
      <Popover id={`popover-positioned-bottom`} className="NotificationPopover">
        <Popover.Content>
          <h5>No notifications</h5>
        </Popover.Content>
      </Popover>
    )
}

export default Notification;