import React, {Component} from 'react';
import { BrowserRouter as Router, Switch, Route} from 'react-router-dom';
import Home from '../home/Home';
import BlogPage from '../blogpage/BlogPage';
import MyAccount from '../myaccount/MyAccount';
import Hashtag from '../../hoc/hashtagcreator/Hashtag';
import HashtagPage from '../hashtagpage/HashtagPage';
import CommunityLanding from '../community/landing/CommunityLanding';
import Interest from '../interests/Interest';
import Statistic from '../stats/Statistic';
import Setting from '../setting/Setting';

class Layout extends Component {
  constructor(props){
    super(props);
    this.state = {

    }
  }

  render() {
    return (
      <Router>
          <Switch>
              <Route path="/cribe-client/" exact><Home /></Route>
              <Route path="/cribe-client/page"><BlogPage /></Route>
              <Route path="/cribe-client/account"><MyAccount /></Route>
              <Route path="/cribe-client/community/landing"><CommunityLanding /></Route>
              <Route path="/cribe-client/interests"><Interest /></Route>
              <Route path="/cribe-client/statistics"><Statistic /></Route>
              <Route path="/cribe-client/hashtag" exact><HashtagPage /></Route>
              <Route path="/cribe-client/hashtag/create"><Hashtag /></Route>
              <Route path="/cribe-client/setting"><Setting /></Route>
          </Switch>
      </Router>
    );
  }
}

export default Layout;
