import React from 'react';
import './Profile.css';
import {Popover} from 'react-bootstrap';
import Avatar from '@material-ui/core/Avatar';
import {NavLink} from 'react-router-dom';


const Profile = () => {
    return(
      <Popover id={`popover-positioned-bottom`} className="ProfilePopover">
        <Popover.Title>
          <div className="ProfileTitle">
            <Avatar style={{width: '50px', height: '50px'}}>S</Avatar>
            <div>
              <h6>User Name</h6>
              <p>User_id</p>
            </div>
          </div>
          <div>
            <button>Subscribe</button>
          </div>
        </Popover.Title>
        <Popover.Content>
          <ul>
            <li><NavLink to="/cribe-client/community/landing">Community</NavLink></li>
            <li><NavLink to="/cribe-client/statistics">Statistics</NavLink></li>
            <li><NavLink to="/cribe-client/interests">Personalise interests</NavLink></li>
          </ul>
          <hr></hr>
          <ul>
            <li><NavLink to="/cribe-client/hashtag">Your hashtag</NavLink></li>
            <li><NavLink to="/cribe-client/hashtag/create">Create you hashtag</NavLink></li>
          </ul>
          <hr></hr>
          <ul>
            <li><NavLink to="/cribe-client/account">Account</NavLink></li>
            <li><NavLink to="/cribe-client/setting">Setting</NavLink></li>
            <li><NavLink to="/help">Help</NavLink></li>
            <li><NavLink to="/logout">Logout</NavLink></li>
          </ul>
        </Popover.Content>
      </Popover>
    )
}

export default Profile;